import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';
import Row from '../row';

const CSS_PREFIX = 'vds-card--dashboard-header';

const CardDashboardHeader = ({ children, className, ...remainingProps }) => (
  <Row
    className={classnames(CSS_PREFIX, className)}
    noGutter
    {...remainingProps}
  >
    {children}
  </Row>
);

CardDashboardHeader.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

CardDashboardHeader.displayName = 'CardDashboardHeader';

export default CardDashboardHeader;
