import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-card--dashboard--mini-number--big';

const CardDashboardMiniNumberBig = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

CardDashboardMiniNumberBig.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

CardDashboardMiniNumberBig.displayName = 'CardDashboardMiniNumberBig';

export default CardDashboardMiniNumberBig;
