import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-card--dashboard-subtitle';

const CardDashboardSubtitle = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

CardDashboardSubtitle.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

CardDashboardSubtitle.displayName = 'CardDashboardSubtitle';

export default CardDashboardSubtitle;
