import React from 'react';
import { string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-card--content-separator';

const CardContentSeparator = ({ className, ...remainingProps }) => (
  <hr
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  />
);

CardContentSeparator.propTypes = {
  /**
   * @ignore
   */
  className: string
};

CardContentSeparator.displayName = 'CardContentSeparator';

export default CardContentSeparator;
