import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-card--dashboard--mini-data-point';

const CardDashboardMiniDataPoint = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

CardDashboardMiniDataPoint.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

CardDashboardMiniDataPoint.displayName = 'CardDashboardMiniDataPoint';

export default CardDashboardMiniDataPoint;
