import React from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-card';
const CSS_STATE = 'vds-state';

const Card = ({ children, className, state, type, ...remainingProps }) => (
  <div
    className={classnames({
      [`${CSS_PREFIX}--${type}`]: Boolean(type),
      [`${CSS_STATE}--${state}`]: Boolean(state)
    }, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

Card.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * State
   */
  state: oneOf([
    'error',
    'success'
  ]),

  /**
   * Type
   */
  type: oneOf([
    'content',
    'content--mini',
    'dashboard',
    'dashboard--mini'
  ])
};

Card.displayName = 'Card';

export default Card;
