import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-card--dashboard-data-vis';

const CardDashboardDataVis = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

CardDashboardDataVis.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

CardDashboardDataVis.displayName = 'CardDashboardDataVis';

export default CardDashboardDataVis;
