import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-card--dashboard--mini-qualifier';

const CardDashboardMiniQualifier = ({ children, className, ...remainingProps }) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </div>
);

CardDashboardMiniQualifier.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

CardDashboardMiniQualifier.displayName = 'CardDashboardMiniQualifier';

export default CardDashboardMiniQualifier;
